function sliderWorkshop() {
    var swiper = new Swiper('.workshop .swiper', {
        slidesPerView: 3,
        spaceBetween: 47,
        navigation: {
            nextEl: ".workshop .swiper-button-next",
            prevEl: ".workshop .swiper-button-prev",
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 20,
            },
            451: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            767: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            993: {
                slidesPerView: 3,
                spaceBetween: 47,
            },
        }
    })
}


function sliderSeason() {
    $(".sliders").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        let swiper1 = new Swiper(`.sliders[data-slider-id="${index}"] .mySwiper`, {
            slidesPerView: 4,
            spaceBetween: 20,
            breakpoints: {
                320: {
                    slidesPerView: 1,
                    spaceBetween: 20,
                },
                769: {
                    slidesPerView: 3,
                    spaceBetween: 30,
                },
                993: {
                    slidesPerView: 4,
                    spaceBetween: 20,
                },
            }
        });
        let swiper2 = new Swiper(`.sliders[data-slider-id="${index}"] .mySwiper2`, {
            slidesPerView: 4,
            spaceBetween: 10,
            navigation: {
                nextEl: `.sliders[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.sliders[data-slider-id="${index}"] .swiper-button-prev`,
            },
            breakpoints: {
                320: {
                    slidesPerView: 1,
                },
                769: {
                    slidesPerView: 3,
                },
                993: {
                    slidesPerView: 4,
                },
            }
        })

        $(`.sliders[data-slider-id="${index}"] .swiper-button-prev`).click(function () {
            swiper1.slidePrev()
        })

        $(`.sliders[data-slider-id="${index}"] .swiper-button-next`).click(function () {
            swiper1.slideNext()
        })
    })
}

